<template>
  <v-data-table
    :headers="headers"
    :items="decks"
    :sort-by="['winrate', 'games']"
    :sort-desc="[true, true]"
    multi-sort
    class="elevation-1 decks-table"
    style="height:100%;position:relative;z-index:1"
    :loading="isLoading"
    :items-per-page="20"
  >
    <template v-slot:item.cardsInDeck="{ item }">
        <Cards v-bind:cards="item.cardsInDeck"/>
    </template>
    <template v-slot:item.winrate="{ item }">
        {{item.winrate}} %
    </template>
  </v-data-table>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  import Cards from "./Cards.vue";
  import * as get from "../../utils/get.js";

  export default {
    components: {
        Cards
    },
    props: {
        filterProp: { type: String },
        timeFrameProp: { type: Number },
        minGamesProp: { type: Number }
    },
    data () {
      return {
        headers: [
          { text: 'Archetype', value: 'archetypeName', sortable: false },
          { text: 'Winrate', value: 'winrate' },
          { text: 'Games', value: 'games' },
          { text: 'Cards', value: 'cardsInDeck', sortable: false }
        ],
        decks: [],
        isLoading: false,
        filter: "",
        timeFrame: 30,
        minGames: 1
      }
    },
    mounted () {
      this.getListDecks();
    },
    methods: {
      async getListDecks (filter = "", timeFrame = 30) {
        this.isLoading = true;
        let listDecks = await get.getDecks(filter, timeFrame);
        listDecks.data.forEach((deck) => {
          if(!deck.archetypeName){
            if(deck.cardsInDeck.length > 22){
              deck.archetypeName = "Expedition deck";
            }
            else deck.archetypeName = deck.factions.join(" / ")
          }
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
          deck.winrate = deck.winrate.toFixed(2);
        });
        let onlyValidDecks = [];
        listDecks.data.forEach((deck) => {
          if(deck.cardsInDeck.length > 13){
            onlyValidDecks = [...onlyValidDecks, deck];
          }
        })
        this.decks = onlyValidDecks;
        console.log(this.decks);
        this.isLoading = false
      }
    },
    watch: {
      filterProp: {
        handler: function(val){
          this.filter = val
          this.getListDecks(val, this.timeFrame, this.minGames)
        },
        deep: true
      },
      timeFrameProp: {
        handler: function(val){
          console.log(val);
          this.getListDecks(this.filter, val, this.minGames);
        },
        deep: true
      },
      minGamesProp: {
        handler: function(val){
          this.getListDecks(this.filter, this.timeFrame, val);
        },
        deep: true
      },
    }
  }
</script>
<style>
  .decks-table {
    position: relative;
  }
  .decks-table col:nth-child(1){
    width: 160px;
  }
  .decks-table col:nth-child(2){
    width: 120px;
  }
  .decks-table col:nth-child(3){
    width: 110px;
  }
  .decks-table td:nth-child(1){
    font-weight: bold;
  }
  .decks-table td:nth-child(2){
    text-align: right !important;
    padding-right:40px;
  }
  .decks-table td:nth-child(3){
    text-align: right !important;
    padding-right:40px;
  }
</style>